import { getAllMv } from '@/request/api/video.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 已选语种(不填则为全部)
      area: '',
      // 已选分类(不填则为全部)
      type: '',
      // 已选筛选(不填则为上升最快)
      order: '',
      // 可选地区
      areas: [{
        name: '全部'
      }, {
        name: '内地'
      }, {
        name: '港台'
      }, {
        name: '欧美'
      }, {
        name: '韩国'
      }, {
        name: '日本'
      }],
      // 可选类型
      types: [{
        name: '全部'
      }, {
        name: '官方版'
      }, {
        name: '原声'
      }, {
        name: '现场版'
      }, {
        name: '网易出品'
      }],
      // 可选排序
      orders: [{
        name: '上升最快'
      }, {
        name: '最热'
      }, {
        name: '最新'
      }],
      //当前地区
      activeArea: '',
      //当前分类
      activeType: '',
      //当前排序
      activeOrder: '',
      // 接收的 MV
      allMvs: []
    };
  },

  mounted() {
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)；连续赋值
    this.activeArea = this.area = this.$route.query.area || '全部';
    this.activeType = this.type = this.$route.query.type || '全部';
    this.activeOrder = this.order = this.$route.query.order || ''; // 获取全部 mv

    this.getAllMv(this.area, this.type, this.order, 96);
  },

  methods: {
    // 获取全部 mv
    async getAllMv(area, type, order, limit) {
      // 显示加载
      this.isLoading = 1;
      const res = await getAllMv(area, type, order, limit); // console.log(res);

      this.allMvs = res.data.data; // console.log('接收的 MV', this.allMvs);
      // 加载完成

      this.isLoading = 0;
    },

    // 换地区
    changeArea(area) {
      if (area !== this.area) {
        this.area = area; // 请求全部 MV 数据

        this.getAllMv(this.area, this.type, this.order, 96); //选项添加 active 类

        this.activeArea = area;
      }
    },

    // 换分类
    changeType(type) {
      if (type !== this.type) {
        this.type = type;
        this.getAllMv(this.area, this.type, this.order, 96);
        this.activeType = type;
      }
    },

    // 换排序
    changeOrder(order) {
      if (order !== this.order) {
        this.order = order;
        this.getAllMv(this.area, this.type, this.order, 96);
        this.activeOrder = order;
      }
    },

    // 对播放量的处理
    changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(0) + "亿";
      } else if (num >= 100000) {
        return (num / 10000).toFixed(0) + "万";
      } else return num;
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  }
};